.Editor .header {
  background-color: white;
  width: 96%;
  margin: auto;
  padding: 6px 0px;
  width: 100%; }
  .Editor .header .logo {
    margin-right: 20px;
    font-size: 22px;
    color: #000; }
  .Editor .header .title {
    font-weight: 900; }
    .Editor .header .title input {
      border: none;
      outline: none;
      font-size: 16px;
      font-family: "Nunito", sans-serif;
      max-width: 100%;
      min-width: 240px;
      margin-right: 20px; }
      .Editor .header .title input::-webkit-input-placeholder {
        font-style: italic !important;
        color: #606060; }
      .Editor .header .title input:-ms-input-placeholder {
        font-style: italic !important;
        color: #606060; }
      .Editor .header .title input::-ms-input-placeholder {
        font-style: italic !important;
        color: #606060; }
      .Editor .header .title input::placeholder {
        font-style: italic !important;
        color: #606060; }
  .Editor .header .state {
    color: #606060;
    height: 100%;
    padding-top: 6px; }
  .Editor .header .options {
    text-align: right; }
    .Editor .header .options .btn {
      padding: 5px 16px;
      background-color: #00a65a;
      font-size: 14px;
      outline: none;
      border: 2px #00a65a solid;
      border-radius: 8px;
      font-family: "Nunito", sans-serif;
      font-weight: 900;
      color: white;
      cursor: pointer;
      -webkit-transition: 0.1s ease-in-out;
      -o-transition: 0.1s ease-in-out;
      transition: 0.1s ease-in-out; }
      .Editor .header .options .btn i.fa {
        margin-right: 8px; }
      .Editor .header .options .btn:hover {
        background-color: white;
        color: #00a65a; }
    .Editor .header .options .badge {
      margin-right: 25px;
      height: 100%;
      font-size: 16px;
      text-align: center; }
      .Editor .header .options .badge span.number {
        margin-right: 4px;
        background-color: #00a65a;
        color: white;
        border-radius: 50%;
        padding: 6px 12px;
        font-weight: 900; }
      .Editor .header .options .badge span.text {
        color: #606060;
        font-style: italic; }

.Editor .editor {
  line-height: 10px; }



