.Viewer {
  height: 100%;
  min-height: 100vh;
  background-color: white;
  padding: 15px; }
  .Viewer .btn {
    background-color: #00a65a;
    color: white;
    background-color: white;
    color: #00a65a;
    border-radius: 50%;
    border: 3px solid #00a65a;
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    width: 50px;
    height: 50px;
    font-size: 20px;
    font-weight: 900;
    outline: none;
    cursor: pointer; }
    .Viewer .btn:hover {
      -webkit-transform: rotate(360deg);
          -ms-transform: rotate(360deg);
              transform: rotate(360deg); }
  .Viewer .btn-fixed {
    position: fixed;
    top: 15px;
    right: 15px;
    z-index: 999999; }
  .Viewer .wrapper-view {
    color: #131313;
    z-index: 10000;
    padding: 10px;
    line-height: 10px; }



